import React from "react";

export class SideBar extends React.Component<{isFixed: boolean, children: any}, {fixed: boolean}>
{
  constructor(props)
  {
    super(props);
    this.state = {
      fixed: false
    }
  }

  onScroll = (ev: Event) => {
    let offset = window.scrollY;
    let container = document.getElementById("content-container");
    this.setState({
      fixed: offset > container.offsetTop
    });
  }

  componentDidMount()
  {
    if(this.props.isFixed == true)
      window.addEventListener("scroll", this.onScroll);
  }

  componentWillUnmount()
  {
    if(this.props.isFixed == true)
      window.removeEventListener("scroll", this.onScroll);
  }

  render()
  {
    return (
      <div className={this.state.fixed == true ? "sidebar-fixed" : "sidebar-not-fixed"}>
        {this.props.children}
      </div>
    )
  }
}